import React from 'react';
import style from './css.module.css';
import Button from "../../Button/Button";
import {buttons_colorType} from "../../../State/state";


type ColorSelectionType =
    {
        ChangeColorValue:(colorSelection:string) => void
        buttons_color:Array<any>
    }
function ColorSelection(props:ColorSelectionType) {


    return(<div className={style.main}>
            <br/>

            Выберите цвет цифр из предложенной ниже палитры <br/><br/>
            {props.buttons_color.map(button =>
                {
                    function color() {
                        props.ChangeColorValue(button.backgroundColor)
                    }
                    return <Button id={button.id} height={button.height} width={button.width}
                                   backgroundColor={button.backgroundColor}  onClick={color}
                    />
                }
            )}
        </div>
    )

}

export default ColorSelection;
