import React from 'react';
import style from './css.module.css';
import Counter from "./Counter/Counter";
import Button, {ButtonType} from "../Button/Button";
import Buttons_Start_Stop from "./Buttons/Buttons_Start_Stop";
import Vectors from "./Vectors/Vectors";
import {buttons_Start_ResetType} from "../../State/state";


export type HeaderType =
    {
        error: boolean
        AddValueCounter: () => void,
        ResetCounter:() => void,
        value_counter:string,
        buttons_Start_Reset:Array<buttons_Start_ResetType>
        color:string
        startvalue:string
        maxvalue: string
    }
function Header(props:HeaderType) {
    return(
        <div className={style.Header}>
            <Vectors startvalue={props.startvalue} maxvalue={props.maxvalue} color={props.color}/>
            <Counter value={props.value_counter} color={props.color} error={props.error}/>
            <Buttons_Start_Stop AddValueCounter={props.AddValueCounter} ResetCounter={props.ResetCounter}
                                buttons_Start_Reset={props.buttons_Start_Reset}/>
        </div>
    )

}

export default Header;
