import React, {useState} from 'react';
import style from './css.module.css';


type VectorsType =
    {
        color:string
        startvalue:string
        maxvalue: string
    }
function Vectors(props:VectorsType) {
    return(
        <div className={style.main}>
            Текущие найстроки<br />
            Значения старта: {props.startvalue}<br />
            Макс. значение: {props.maxvalue}<br />
            Выбраный цвет: {props.color}<br />

        </div>
    )

}

export default Vectors;
