import React from 'react';
import style from './css.module.css';
import ValueSelection from "./Value_selection/Value_selection";
import ColorSelection from "./Color_selection/Color_selection";
import {buttons_colorType} from "../../State/state";


 export type SettingsType =
    {
        ChangeStartValue:(startvalue:string) => void
        ChangeColorValue:(colorSelection:string) => void
        ChangeMaxValue:(maxvalue:string) => void
        buttons_color:Array<any>
        newMaxValue : string,
        newStartValue : string,
        UpdateMaxValue: (maxvalue:string) => void
        UpdateStartValue: (startvalue:string) => void
        ErrorValues: (inputerror:boolean) => void

    }
function Settings(props:SettingsType) {
    return(
        <div className={style.main}>
            <ValueSelection ChangeStartValue={props.ChangeStartValue}
                            ChangeMaxValue={props.ChangeMaxValue}
                            newMaxValue={props.newMaxValue}
                            newStartValue={props.newStartValue}
                            UpdateMaxValue={props.UpdateMaxValue}
                            UpdateStartValue={props.UpdateStartValue}
                            ErrorValues={props.ErrorValues}

            />
            <ColorSelection ChangeColorValue={props.ChangeColorValue}
                            buttons_color={props.buttons_color}/>
        </div>
    )

}

export default Settings;
