import {rerenderEntireTree} from "../render";


export type StateType = {
	Header : HeaderType,
	Settings : SettingsType
}
type HeaderType = {
	buttons_Start_Reset: Array<buttons_Start_ResetType>
}
type SettingsType = {
	buttons_Start_Reset: Array<buttons_colorType>
}
export type buttons_Start_ResetType =
	{
		id:string,
		height: string,
		width:string,
		backgroundColor:string,
		content:string,
		borderRadius:string,
		disabled:boolean,
	}
export type buttons_colorType =
	{
		id:string,
		height: string,
		width:string,
		backgroundColor:string,
	}
export let state = {
	Header: {
		buttons_Start_Reset: [
			{
				id: "1", height: "100px", width: "100px", backgroundColor: "green",
				content: "start", borderRadius: "100%", disabled: false
			},
			{
				id: "2", height: "100px", width: "100px", backgroundColor: "red",
				content: "reset", borderRadius: "100%", disabled: false
			}
		],
		MaxValue: "5",
		StartValue: "1",
	},
	Settings: {
		buttons_color: [
			{id: "1", height: "50px", width: "50px", backgroundColor: "white"},
			{id: "2", height: "50px", width: "50px", backgroundColor: "orange"},
			{id: "2", height: "50px", width: "50px", backgroundColor: "blue"},
			{id: "2", height: "50px", width: "50px", backgroundColor: "black"},
			{id: "2", height: "50px", width: "50px", backgroundColor: "green"},
			{id: "2", height: "50px", width: "50px", backgroundColor: "yellow"},
			{id: "2", height: "50px", width: "50px", backgroundColor: "brown"},
			{id: "2", height: "50px", width: "50px", backgroundColor: "purple"}
		],
		MaxValue: "5",
		StartValue: "1",

	}
}
export const UpdateMaxValue = (maxvalue:string) => {
	state.Settings.MaxValue = maxvalue
	rerenderEntireTree(state)

}
export const UpdateStartValue = (startvalue:string) => {
	state.Settings.StartValue = startvalue
	rerenderEntireTree(state)
}
export const UpdateMaxValueVectors = (max:string) => {
	state.Header.MaxValue = max
	rerenderEntireTree(state)
}
export const UpdateStartValueVectors = (start:string) => {
	state.Header.StartValue = start
}