import React,{ChangeEvent} from 'react';
import style from './css.module.css';
////// False - Квадрат
////// True - Круг

export type ButtonType =
    {
        height:string,
        width:string,
        borderRadius?:string,
        backgroundColor?:string,
        content?: string;
        onClick?: () => void
        disabled?: boolean
        id?: string
    }


function Button(props: ButtonType) {
    let style = {
        height: props.height,
        width: props.width,
        borderRadius: props.borderRadius,
        backgroundColor: props.backgroundColor,
    }

    let zero = () => {}
    if (props.onClick) zero = props.onClick


    return (
        <button id={props.id} onClick={zero} style={style} disabled={props.disabled} value={style.backgroundColor} >
            {props.content}
        </button>

    )


}

export default Button;
