import React, {ChangeEvent} from 'react';
import style from './css.module.css';
import Button from "../../Button/Button";
import {rerenderEntireTree} from "../../../render";


type ValueSelectionType =
    {
        ChangeStartValue: (startvalue: string) => void
        ChangeMaxValue: (maxvalue: string) => void
        newMaxValue: string,
        newStartValue: string,
        UpdateMaxValue: (maxvalue:string) => void
        UpdateStartValue: (startvalue:string) => void
        ErrorValues: (inputerror:boolean) => void

    }

function ValueSelection(props: ValueSelectionType) {
    let max = ""
    let start = ""

    function OnChangeMaxValue(event: ChangeEvent<HTMLInputElement>) {
        max = String(event.currentTarget.value)
        props.UpdateMaxValue(max)
    }

    function OnChangeStartValue(event: ChangeEvent<HTMLInputElement>) {
        start = String(event.currentTarget.value)
        props.UpdateStartValue(start)

    }

    function SentValue() {
debugger
        if (Number(props.newMaxValue) <= Number(props.newStartValue) || Number(props.newStartValue) <= -1) {
            props.ErrorValues(true)
        } else {
            props.ChangeMaxValue(props.newMaxValue)
            props.ChangeStartValue(props.newStartValue)
            props.ErrorValues(false)
        }
    }

    return (
        <div className={style.main}>
            Введите максимальное значение <br/>( Не меньше и не равное значению старта)<input onChange={OnChangeMaxValue} value={props.newMaxValue}/>
            <Button height={"50px"} width={"100px"} content={"Потвердить"} onClick={SentValue}/>
            Введите Значение старта <br />( Не меньше 0 )<input onChange={OnChangeStartValue} value={props.newStartValue}/>
        </div>
    )

}

export default ValueSelection;
