import React, {useState} from 'react';
import style from './css.module.css';


type CounterType =
    {
        color:string
        value:string
        error:boolean
    }
function Counter(props:CounterType) {
    let colorError = {
        color: "red"
    }
    let color = {
        color:props.color,
    }
    return(
        <div className={style.Counter}>

            {props.error && <span style={colorError}>{props.value}</span> || <span style={color}>{props.value}</span>}
        </div>
    )

}

export default Counter;
