import React, {useState} from 'react';
import style from './css.module.css';
import Button from "../../Button/Button";
import {buttons_Start_ResetType} from "../../../State/state";


type Buttons_Start_StopType =
    {
        AddValueCounter: () => void
        ResetCounter:() => void
        buttons_Start_Reset:Array<buttons_Start_ResetType>
    }


function Buttons_Start_Stop(props:Buttons_Start_StopType) {
    let fucntion = () => {}


    return(
        <div className={style.main}>
            {props.buttons_Start_Reset.map(button =>
            {
                if (button.id === "1") fucntion = props.AddValueCounter
                else  fucntion = props.ResetCounter
                return <Button height={button.height} width={button.width}
                               backgroundColor={button.backgroundColor} content={button.content}
                               borderRadius={button.borderRadius} onClick={fucntion} disabled={button.disabled}
                />
            }
            )}
        </div>
    )

}

export default Buttons_Start_Stop;
