import ReactDOM from "react-dom";
import App from "./App";
import React from "react";
import {UpdateMaxValue,UpdateStartValue,UpdateMaxValueVectors, UpdateStartValueVectors} from "./State/state";

export let rerenderEntireTree = (state) =>
{
	ReactDOM.render(
		<App state={state}
			 UpdateMaxValue={UpdateMaxValue}
			 UpdateStartValue={UpdateStartValue}
			 UpdateMaxValueVectors={UpdateMaxValueVectors}
			 UpdateStartValueVectors={UpdateStartValueVectors}
		/>,
		document.getElementById('root'));
}